import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getDatabase, ref, get } from "firebase/database";
import {apiKey, authDomain, projectId,databaseURL,appId} from "./constant";
import {
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
  onAuthStateChanged,
  signOut,
} from "firebase/auth";
import {useMutation, useQuery} from "react-query";


const firebaseConfig = {
  apiKey,
  authDomain,
  databaseURL,
  projectId,
  appId
};

const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const auth = getAuth();
const provider = new GoogleAuthProvider();
const database = getDatabase(app);

async function fetchUser() {
  return new Promise((resolve) => {
    onAuthStateChanged(auth, async (user) => {
      const updateUser = user ? await adminUser(user) : user;
      resolve(updateUser);
    });
  });
}

async function adminUser(user){
  return get(ref(database, 'admins'))
    .then((snapshot)=>{
      if(snapshot.exists()){
        const admins = snapshot.val();
        const isAdmin = admins.includes(user.uid);
        return { ...user, isAdmin }
      }
      return user;
    })
}

//   
async function loginUser() {
  await signInWithPopup(auth, provider);
  return auth.currentUser;
}

//   
async function logoutUser() {
  await signOut(auth);
}

// React Query       
export function useAuth() {
  const { data, status } = useQuery('user', fetchUser);
  const { mutate: login } = useMutation(loginUser);
  const { mutate: logout } = useMutation(logoutUser);

  return {
    user: data,
    status,
    login,
    logout,
  };
}