import React from 'react';
import { useNavigate} from "react-router-dom";
import {useAuth} from "../../common/auth";
import {getPortfolio} from "../../libs/firebase/firebase";
import {useQuery} from "react-query";
import PortfolioList from "./portfolio.list";

const Portfolio = () => {

  const {user, status} = useAuth();
  const {isLoading, error, data} = useQuery(["portfolio"],getPortfolio);
  const navigate = useNavigate();


  if(status === "loading") return false;

  const writeBtn = () =>{
    const { isAdmin } = user;
    if(!isAdmin) {
      return alert("  .");
    }
    navigate("/portfolioWrite")
  };


  return (
    <>
      {isLoading && <p>...</p>}
      {error && <p>{error}</p>}
      {data &&
        <div className="portfolio-wrap">
          <button type="button" className="btn-write" onClick={writeBtn}></button>
          <PortfolioList data={data}/>
        </div>
      }
    </>

  );
};

export default Portfolio;