import React, {useRef, useState} from 'react';
import { bucket, region} from "../../libs/aws/awsSetting";
import AWS, { S3 } from "aws-sdk";
import {accessKeyId, secretAccessKey} from "../../common/constant";
import {getPortfolio, writePortfolio} from "../../libs/firebase/firebase";
import {useQuery} from "react-query";


const PortfolioWrite = () => {
  const [products, setProducts] = useState({});
  const {isLoading, error, data} = useQuery(["portfolio"],getPortfolio);

  const thumbRef = useRef(null);

  const handelChange = e =>{
    const {name, type, value, files} = e.target;


    if(type === "file"){
      const file = files[0];
      const s3 = new AWS.S3({
        accessKeyId,
        secretAccessKey,
        region,
      });

      const params = {
        Bucket: bucket,
        Key: file.name,
        Body: file,
        ACL: 'public-read', //       .
      };


      s3.upload(params, (err, data)=>{
        if(err){
          console.error(err)
        }else{
          setProducts({
            ...products,
            thumb:data.Location
          });
        }
      })
      return false;
    }

    setProducts({
      ...products,
      [name] : value,
    })
  }

  const handelSubmit = () => {

    const imgW = thumbRef.current?.clientWidth;
    const imgH = thumbRef.current?.clientHeight;

    let product;
    if(imgW - imgH > 80) {
      product = {
        ...products,
        thumbWidth : true
      }
    }else{
      product = {
        ...products,
        thumbWidth : false
      }
    }

    if(products?.skill){
      const splitSkill = products?.skill.split(",");
      product = {
        ...product,
        skill:splitSkill
      }
    }

    writePortfolio(product, data.length)
      .then(()=>{
        alert("  .");
        window.location.reload();
      });
  }

  return (
    <div className="write-box">
      <form action="">
        <ul>
          <li>
            <label htmlFor="name"> </label>
            <input
              type="text"
              name="name"
              id="name"
              value={products.name ?? ""}
              placeholder=" "
              onChange={handelChange}
            />
          </li>
          <li>
            <label htmlFor="day"> </label>
            <input
              type="text"
              id="day"
              name='day'
              value={products.day ?? ""}
              placeholder="YYYYMM - YYYYMM"
              onChange={handelChange}
            />
          </li>
          <li>
            <p className="label-tt"> </p>
            <label className={products?.thumb ? "thumb" : "thumb-box"} htmlFor="thumb" >
              {products?.thumb && <img src={products.thumb} ref={thumbRef} alt=""/>}
            </label>
            <input
              type="file"
              id="thumb"
              name='thumb'
              placeholder=" "
              onChange={handelChange}
              className="input-file"
            />
          </li>
          <li>
            <label htmlFor="link"> URL</label>
            <input
              type="text"
              name="link"
              value={products.link ?? ""}
              id="link"
              placeholder=" URL"
              onChange={handelChange}
            />
          </li>
          <li>
            <label htmlFor="company"></label>
            <input
              type="text"
              name="company"
              id="company"
              value={products.company ?? ""}
              placeholder=""
              onChange={handelChange}
            />
          </li>
          <li>
            <label htmlFor="skill"> </label>
            <input
              type="text"
              name="skill"
              id="skill"
              value={products.skill ?? ""}
              placeholder="  "
              onChange={handelChange}
            />
          </li>
          <li>
            <label htmlFor="desc"> </label>
            <textarea
              name="desc"
              id="desc"
              value={products.desc ?? ""}
              onChange={handelChange}
            >
            </textarea>
          </li>
          <li>
            <label htmlFor="work"> </label>
            <textarea
              name="work"
              id="work"
              value={products.work ?? ""}
              onChange={handelChange}
            >
            </textarea>
          </li>
        </ul>
      </form>
      <div className="btn-box">
        <button type="button" className="btn-save" onClick={handelSubmit}></button>
      </div>
    </div>
  );
};

export default PortfolioWrite;